import { create } from 'zustand';
import { persist, createJSONStorage } from 'zustand/middleware';

// Definição dos Tipos (O que nossa carteira guarda)
type PlanTier = 'free' | 'bronze' | 'prata' | 'ouro' | 'platina';

interface UserState {
  // Dados do Usuário
  name: string;
  patinhas: number;
  plan: PlanTier;
  
  // Inventário (IDs dos capítulos comprados: "mangaId-capId")
  unlockedChapters: string[];

  // Ações (O que podemos fazer)
  addPatinhas: (amount: number) => void;
  setPlan: (tier: PlanTier) => void;
  unlockChapter: (mangaId: number | string, chapterId: number | string, cost: number) => boolean;
  isUnlocked: (mangaId: number | string, chapterId: number | string) => boolean;
}

export const useUserStore = create<UserState>()(
  persist(
    (set, get) => ({
      // Estado Inicial (Começamos com 5 patinhas grátis para teste)
      name: 'Caçador Novato',
      patinhas: 5, 
      plan: 'free',
      unlockedChapters: [],

      // Adicionar Saldo (Simula compra de pacote)
      addPatinhas: (amount) => set((state) => ({ patinhas: state.patinhas + amount })),

      // Mudar Plano
      setPlan: (tier) => set({ plan: tier }),

      // Tentar Comprar Capítulo
      unlockChapter: (mangaId, chapterId, cost) => {
        const { patinhas, unlockedChapters } = get();
        const key = `${mangaId}-${chapterId}`;

        // Se já tem, não gasta
        if (unlockedChapters.includes(key)) return true;

        // Se não tem dinheiro
        if (patinhas < cost) return false;

        // Compra efetuada
        set({
          patinhas: patinhas - cost,
          unlockedChapters: [...unlockedChapters, key],
        });
        return true;
      },

      // Verificar se já comprou
      isUnlocked: (mangaId, chapterId) => {
        const key = `${mangaId}-${chapterId}`;
        return get().unlockedChapters.includes(key);
      },
    }),
    {
      name: 'gato-comics-storage', // Nome da chave no LocalStorage
      storage: createJSONStorage(() => localStorage),
    }
  )
);